@media screen and (max-width: 767px) { 
    #loginForm { 
        width : 100%;
        height:  100vh;
    }

    .adminaa input { 
        position: relative;
        border-radius: 20px;
        width: 250px;
        margin-top: 20px;
    }
}
@media screen and (min-width: 767px) { 
    #loginForm { 
        width : 100%;
        height:  100vh;
    }

    .adminaa input { 
        position: relative;
        border-radius: 20px;
        width: 450px;
        left: 30%;
        margin-top: 20px;
        
    }
}