@media screen and (min-width: 768px) {
    .container-tailieu { 
        width: 100%;
        background-color: antiquewhite;
        font-family: 'Times New Roman', Times, serif;
    }
    .list-tailieu {
        width: 100vh;
        min-height: 1000px;
        display:  grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 10px;
      
       
        
   }
   .list-tailieu li  { 
    width: 300px;
    height: 400px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    list-style: none;
    background-color: aliceblue;
    border-radius: 10px 10px 0px 0px;
   margin-top: 80px;
    text-align: center;
  
   }
   .list-tailieu li img { 
    width: 100%;
    height: 200px;
    border-radius: 10px 10px 0px 0px;
   }
    .list-tailieu li h5 {
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: black;
    
   }
   .list-tailieu li a { 
    text-decoration: none;
    color: white;
    font-size: 15px;
    font-weight: bold;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
   }
   
}

@media screen and (max-width: 767px)  { 
    .menu-3{ 
        display: none;
    }
    .container-tailieu { 
       width: 100vh;
        background-color: antiquewhite;
        font-family: 'Times New Roman', Times, serif;
    }
    .list-tailieu {
        width: 100vh;
        min-height: 1000px;
        display:  grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 1px;
      
       
        
   }
   .list-tailieu li  { 
    width: 200px;
    height: 400px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    list-style: none;
    background-color: aliceblue;
    border-radius: 10px 10px 0px 0px;
   margin-top: 100px;
    text-align: center;
  
   }
   .list-tailieu li img { 
    width: 100%;
    height: 250px;
    border-radius: 10px 10px 0px 0px;
   }
    .list-tailieu li h5 {
    text-align: center;
    font-size: 17px;
    font-weight: bold;
    color: black;
   }
   .list-tailieu li a { 
    text-decoration: none;
    color: white;
    font-size: 18px;
    font-weight: bold;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
   }
}