        width:  100%;
        height: 100px;
        background-color: white;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    
    
    }
    .luachon button { 
        position: relative;
        top: 10%;
        width: 150px;
        height : 50px;
        font-weight: bold;
        font-size: 15px;
        margin-left: 20px;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    }

   
    .parentakhoahoc { 
        background-color :#F9F5FA;
        width: 100%; 
        max-height: 2200px;
    }
    .div1khoahoc { 
        width: 100%;
        height: 850px;
        background-color :#F9F5FA;
        color: #1C3967;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
        border-radius : 0px 0px 20px 0px;
       
       
    }
    .gobackhome { 
        width:  150px;
        height: 30px;
        color : black;
       

    }
    .div1khoahoc h1 { 
        font-family: Georgia, serif;

    }
    .div1khoahoc img { 
        width: 100%;
        height : 300px;
    }


    /* DIV2 */
    .div2khoahoc { 
       
     width: 100%;
     /* max-height: 100px; */
     height: 500px;
     /* background-color :#F9F5FA; */
     background-color: gray;
     background-image: url('http://dongnaiart.edu.vn/wp-content/uploads/2022/01/hinh-nen-powerpoint-nen-trang-1.jpg');
     background-size: cover;
     background-repeat: no-repeat;
    
     
    }
    .hienthithongtin { 
     
       display: flex;
    
       justify-content: center ;
        align-items: center;
        width: 100%;
        height:50px;
        background-color :#EEEEEE;
    }
    .btndiv2 { 
        text-align: center;
        width: 100px;
        height: 40px;
       font-size: 10px;
       font-weight: bold;
       z-index: 1;
       box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
       border-radius: 5px;
        }
        .btndiv2:hover { 
            transform: scale(1.3);
            z-index: 2;
            background-color: #ab71a7;
            color: white;
        }


        /* DIV3 */
   .div3khoahoc{ 
    width: 100%; 
    height: 400px;
   
   }
   .div3khoahoc ul { 
    list-style: none;
   }
   .div3khoahoc ul li img  { 
    width: 30px;
    height: 30px;
    border-radius:50px;
   }
   .chuathongtin { 
    width: 100%;
    max-height: 100px;
    
    border-radius: 10px;
    background-color: #cbced1;
   }
   .div3khoahoc ul li strong  { 
    color: #0056B3;
   }

   .imgdanhgia { 
    width: 300px;
    height: 300px;
    pointer-events: none;
}
   
}

