
/* 
   ĐIỆN THOẠI */
@media screen and (max-width: 767px) { 
     .containerkh {
        background-color: antiquewhite;
        width: 100%;
        min-height: 400px;
        overflow-y: auto;    
         display: grid;
         grid-template-columns: 1fr;
         grid-template-rows: 100px 1fr 1fr;
     }
     .kh1 h1 {
         text-align: center;
     }
     .kh2 {
          width: 100%;
         min-height: 400px;
         
         
        }
     
     .kh2 ul li{ 
        list-style-type : none;
        width:  100%;
        height: 50px;
        border: 1px solid black;
     
        right: 20px;
        text-align :  center;
        margin-top : 10px;
     border-radius: 20px;   
     background-color: white;
     font-weight: bold;
     }
     .kh2  ul li:hover { 
     background-color: burlywood;
      transform: scale(1.1);
      color: white;
      box-shadow: 1px 1px 5px 1px gray;
     }
     .kh2 ul {
        display:  flex ;
        flex-direction: column;   
     }
     .kh3 { 
        width:  100%;
        max-height: 500px;
        overflow-y: auto;
     }
     .tabletruycap tr th {
      font-size: 20px;
      font-weight: bold;
      color: green;
     }
     .tabletruycap  tr td strong {
       font-size: 17px;
      font-weight: bold;
      color: red;
     }
     .tabletruycap  tr td  {
      font-size: 14px;
   
    
    }
     
     .loi404 {
      width: 300px;
      height: 300px;
       position: relative;
       top:  10%;
       left:  10%;
  }
    
}
    

      /* MÁY TÍNH */

@media screen and (min-width: 768px) { 
    .containerkh {
       background-color: antiquewhite;
       width: 100%;
       min-height: 1000px;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 100px 1fr 1fr;
    }
    .kh1 h1 {
        text-align: center;
    }
    .kh2 {
         width: 100%;
         height: 500px;
    }
    .kh2 ul li{ 
       list-style-type : none;
       width:  100%;
       height: 50px;
       border: 1px solid black;
       position: relative;
       right: 20px;
       text-align :  center;
       margin-top : 10px;
    border-radius: 20px;   
    background-color: white;
    font-weight: bold;
    }
    .kh2  ul li:hover { 
    background-color: burlywood;
     transform: scale(1.1);
     color: white;
     box-shadow: 1px 1px 5px 1px gray;
    }
    .kh2 ul {
       display:  flex ;
       flex-direction: column;   
    }
    .kh3 {
       width:  100%;
       max-height: 500px;
       overflow-y: auto;
      
    }

    .tabletruycap tr th {
      font-size: 25px;
