

@media screen and (min-width: 767px)  {
    .nt-container { 
        display: grid;
        max-height: 400px;
        width: 100%;
        overflow: auto;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
   
        
    }
    
    
    .nt-1  { 
        z-index : 999;
        width: 100%;
     background-color:white;
        position: fixed;
        color: black;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    }
    .nt-1 p {
        font-size:  25px;
    }
    .nt-2 {
        width: 100%;
        display:  grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
        background-size : cover;
        background-image: url('https://payos.vn/wp-content/uploads/sites/13/2023/11/Bg-Hd.svg');
        background-repeat: no-repeat;
        place-items: center;
        margin-top: 50px;
    }
    
  
    .image-nt2 img { 
        width : 400px;
        height: 400px
    }
    .title-nt2 p { 
        font-size: 60px;
        font-weight: bold;
    }
    .title-nt2 img {
         width: 80px;
         height: 30px;
    }
    .nt-3 img {
        width: 200px;
        height: 130px;
       }
       .nt-3 p {
         font-size: 20px;
         color: #715353;
       }

       .nt-4 { 
        width: 100%;
         height: 500px;
      
    display: grid;
    grid-template-rows: 1fr 3fr;
    place-items: center;
        
       }

       .nt-4 input { 
        width: 500px;
        height: 70px;
        border-radius: 10px;
       }
       .formpay { 
        border-radius: 20px;
        display : grid;
        place-items: center;
        width: 60%;
        height: 400px;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
       }
       .nt-5  { 
        width : 100%;
        min-height: 400px;
        overflow-y: auto;
       }

       .nt-5 tr { 
        width: 1000px;

       }

       .nt-5 { 
        width: 100%;
        min-height :  400px;
        overflow-y: auto;
       }
     
       .nt-5 tr th {
        background-color: #FFF1E7;
        font-size : 15px;
        font-weight: 600;
        border-bottom: 1px solid rgb(208, 200, 200);
       }

       .nt-5 tr td {
        font-size: 14px;
        font-weight: 400;
        border-bottom: 1px solid rgb(208, 200, 200);
       }
       .contact img { 
        z-index: 999;
        position: fixed;
        width: 100px;
        height: 80px;
        border-radius: 40px 40px 40px 40px;
        top: 83%;
       }

      
    
}

