.renderKhoaHoc ul li { 
    list-style: none;
}
.renderKhoaHoc ul {
    background-color: azure;
    border-radius: 30px;
    width: 100%;
    text-align: center;
    justify-content: center;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    margin-top: 30px;
}
.renderKhoaHoc {
    display: grid; 
    grid-template-columns: 1fr;
    
}
.div3ad { 
   
    width: 100%;
    height: auto; 
    overflow-y: auto;
}