@media screen and (max-width: 768px) {
    .hocthu1 { 
        width: 100%;
        max-height: 400px;

    }
    .menumbhocthu { 
 
     margin-top: -70px;     
    }

    .hocthu2 {
       
        margin-top: 20px;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
        width: 100%;
        height: 200px;
        border-radius: 50px;
        display: grid;
        grid-template-rows: 1fr 1fr 1fr;
        place-items: center;
        
    }
    .hocthu2 p { 
        font-weight: bold;
        font-size: 20px;
        font-family: Georgia, 'Times New Roman', Times, serif;
    }
    .bthocthu2{ 
        width:  60%;
        height: 40px;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
        border-radius: 10px;
        
    }
    .bthocthu2 button  {
        width:  100%;
        height: 40px;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
        border-radius: 10px;
        
    }
    .roothocthu { 
      max-width: 100%;
    }
    .videohocthu { 
        display: grid;
       grid-template-rows: 1fr 1fr;
    }
    .khoahocthu img { 
        width: 100px;
        height: 50px;
        border-radius: 20px;
      }
}


@media screen and (min-width: 768px) {
  

    
     .menuhocthu { 
        margin-top: 0px;
     }

     .hocthu2 {
       
        width:100%;
        height: 400px;
       
        display: grid;
        grid-template-rows: 1fr 1fr 1fr;
        place-items: center;
        
    }
    .bodyhocthu2 { 
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
        width: 100%;
        height: 200px;
        border-radius: 50px;
        margin-top: 100px;

    }
  

     
    .hocthu2 p { 
        font-weight: bold;
        font-size: 20px;
        font-family: Georgia, 'Times New Roman', Times, serif;
    }
     
    .bthocthu2 button  {
        width:  60%;
        height: 40px;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
        border-radius: 10px;
        
    }
    .hocthu3 { 
        box-shadow: 1px 1px 10px 1px gray;
    }
    .roothocthu { 
        max-width: 100%;
      }
      .videohocthu { 
          display: grid;
         grid-template-rows: 1fr 1fr;
      }
      .danhmuchocthu h3 { 
         color: yellow;
      }
      .khoahocthu img { 
        width: 100px;
        height: 50px;
        border-radius: 20px;
      }
      
}