/* =================================================================
   1. IMPORTS & BIẾN TOÀN CỤC (CSS VARIABLES)
   - Đưa tất cả @import lên đầu.
   - Định nghĩa các biến màu sắc, font chữ để tái sử dụng.
   ================================================================= */
@import url('https://fonts.googleapis.com/css?family=Mukta:700');
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');

:root {
    --font-primary: 'Montserrat', sans-serif;
    --font-button: 'Mukta', sans-serif;
    
    --color-primary: #FF4B2B;
    --color-gradient-start: #FF416C;
    --color-white: #FFFFFF;
    --color-dark: #282936;
    --color-dark-text: #333;
    --color-light-gray: #f6f5f7;
    --color-gray-input: #eee;
    --color-gray-border: #DDDDDD;
    --color-red: red;

    --shadow-main: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    --shadow-modal: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

/* =================================================================
   2. STYLES CƠ BẢN (MOBILE-FIRST)
   - Các styles chung cho toàn bộ trang.
   - Giao diện mặc định cho màn hình nhỏ (dưới 768px).
   ================================================================= */

/* body {
    font-family: var(--font-primary);
    background: var(--color-light-gray);
    margin: 0;
} */

.LoginRegis {
	scrollbar-width: none;
    display: flex;
    justify-content: center; /* Canh giữa container trên màn hình lớn */
    align-items: flex-start; /* Canh trên cùng */
    min-height: 100vh;
    padding-top: 100px;
} 

h1 {
    font-weight: bold;
    margin: 0;
}

h2 {
    text-align: center;
}

p {
    font-size: 14px;
    font-weight: 100;
    line-height: 20px;
    letter-spacing: 0.5px;
    margin: 20px 0 30px;
}

a {
    color: var(--color-dark-text);
    font-size: 14px;
    text-decoration: none;
    margin: 15px 0;
}

button {
    border-radius: 20px;
    border: 1px solid var(--color-primary);
    background-color: var(--color-primary);
    color: var(--color-white);
    font-size: 12px;
    font-weight: bold;
    padding: 12px 45px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: transform 80ms ease-in;
    cursor: pointer;
}

button:active {
    transform: scale(0.95);
}

button:focus {
    outline: none;
}

button.ghost {
    background-color: transparent;
    border-color: var(--color-white);
}

form {
    background-color: var(--color-white);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 50px;
    height: 100%;
    text-align: center;
}

input {
    background-color: var(--color-gray-input);
    border: none;
    padding: 12px 15px;
    margin: 8px 0;
    width: 100%; /* Dùng 100% để linh hoạt */
    box-sizing: border-box; /* Quan trọng khi dùng padding và width 100% */
}

/* --- Container chính của Form --- */
.container {
    background-color: var(--color-white);
    border-radius: 10px;
    box-shadow: var(--shadow-main);
    position: relative;
    overflow: hidden;
    width: 100%;
    max-width: 470px ;
    min-height: 480px;
    margin: 0 15px;  
}

.form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
}

.sign-in-container {
    left: 0;
    width: 50%;
    z-index: 2;
}

.sign-up-container {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
}

.container.right-panel-active .sign-in-container {
    transform: translateX(100%);
}

.container.right-panel-active .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: show 0.6s;
}

.overlay-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: transform 0.6s ease-in-out;
    z-index: 100;
}

.container.right-panel-active .overlay-container {
    transform: translateX(-100%);
}

.overlay {
    background: var(--color-gradient-start);
    background: linear-gradient(to right, var(--color-primary), var(--color-gradient-start));
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    color: var(--color-white);
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
    transform: translateX(50%);
}

.overlay-panel {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    text-align: center;
    top: 0;
    height: 100%;
    width: 50%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
}

.overlay-left {
    transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
    transform: translateX(0);
}

.overlay-right {
    right: 0;
    transform: translateX(0);
}

.container.right-panel-active .overlay-right {
    transform: translateX(20%);
}

@keyframes show {
    0%, 49.99% {
        opacity: 0;
        z-index: 1;
    }
    50%, 100% {
        opacity: 1;
        z-index: 5;
    }
}

.social-container {
    margin: 20px 0;
}

.social-container a {
    border: 1px solid var(--color-gray-border);
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px;
    height: 40px;
    width: 40px;
}

/* --- Các hiệu ứng DotLottie --- */
.dotlottie-login, .dotlottie-register { 
    display: none; /* Ẩn trên mobile và tablet theo thiết kế cũ */
}

.dotlottie-login1 { /* Class này chỉ có trong media query cũ của desktop */
    display: none;
}

.dotlottie-logina {
    position: absolute;
    top: -80px;
    right: -130px;
    width: 500px;
    height: 250px;
}

.success-after-register, .success-after-login {
    position: relative;
    z-index: 1000;
    width: 70px;
    height: 50px;
    bottom: 40px;
}
.success-after-register { 
    left: 185px;
}
.success-after-login { 
    left: 210px;
}

/* --- Button "Learn More" --- */
#button-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

#button-wrapper .learn-more {
    width: 12rem;
    position: relative;
    display: inline-block;
    cursor: pointer;
    outline: none;
    border: 0;
    vertical-align: middle;
    text-decoration: none;
    background: transparent;
    padding: 0;
    font-size: inherit;
    font-family: var(--font-button);
}

#button-wrapper .learn-more .circle {
    transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
    position: relative;
    display: block;
    margin: 0;
    width: 3rem;
    height: 3rem;
    background: var(--color-dark);
    border-radius: 1.625rem;
}

#button-wrapper .learn-more .circle .icon {
    transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    background: var(--color-white);
}

#button-wrapper .learn-more .circle .icon.arrow {
    left: 0.625rem;
    width: 1.125rem;
    height: 0.125rem;
    background: none;
}

#button-wrapper .learn-more .circle .icon.arrow::before {
    position: absolute;
    content: '';
    top: -0.25rem;
    right: 0.0625rem;
    width: 0.625rem;
    height: 0.625rem;
    border-top: 0.125rem solid var(--color-white);
    border-right: 0.125rem solid var(--color-white);
    transform: rotate(45deg);
}

#button-wrapper .learn-more .button-text {
    transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 0.75rem 0;
    margin: 0 0 0 1.85rem;
    color: var(--color-dark);
    font-weight: 700;
    line-height: 1.6;
    text-align: center;
    text-transform: uppercase;
}

#button-wrapper .learn-more:hover .circle {
    width: 100%;
}

#button-wrapper .learn-more:hover .circle .icon.arrow {
    background: var(--color-white);
    transform: translate(1rem, 0);
}

#button-wrapper .learn-more:hover .button-text {
    color: var(--color-white);
}

/* --- Modal & Captcha --- */
.modal { 
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 1050;
}
 
.modal-dialog {
    margin: 10% auto;
    max-width: 500px;
}

.modal-content { 
    box-shadow: var(--shadow-modal);
}

.captcha {
    border-radius: 10px;
    position: relative;
    font-size: 36px;
    font-weight: bold;
    text-align: center;
    width: 250px;
    height: 70px;
    background: #f0f0f0;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color-dark-text);
    user-select: none;
}

.captcha span {
    display: inline-block;
    position: relative;
    z-index: 10;
    color: rgba(0, 0, 0, 0.9);
    transform: rotate(10deg) skew(10deg);
    animation: shake 1s ease-in-out infinite;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    letter-spacing: 4px;
}

.captcha span:nth-child(odd) {
    font-size: 30px;
}
.captcha span:nth-child(even) {
    font-size: 50px;
}

.captcha span.crossed {
    text-decoration: line-through;
    color: var(--color-red);
}

@keyframes shake {
    0% { transform: rotate(10deg) translateX(0); }
    50% { transform: rotate(-10deg) translateX(5px); }
    100% { transform: rotate(10deg) translateX(0); }
}

.captcha::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('https://www.transparenttextures.com/patterns/stonewall.png');
    opacity: 0.3;
    z-index: 5;
}

.captcha .noise {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('https://www.transparenttextures.com/patterns/diag-stripes-light.png');
    opacity: 0.4;
    z-index: 5;
}

.captcha span.glow {
    text-shadow: 0 0 10px rgba(255, 255, 255, 0.7);
}

/* --- Footer --- */
.footer-Loginris {
	position: absolute;
	top: 50%;
    background-color: #222;
    color: var(--color-white);
    font-size: 14px;
    text-align: center;
    padding: 10px 0;
    /* position: fixed; có thể gây vấn đề trên mobile, cân nhắc đổi thành static */
    width: 100%;
	z-index: 999;
}

footer p {
    margin: 10px 0;
}

footer i {
    color: var(--color-red);
}

footer a {
    color: #3c97bf;
    text-decoration: none;
}

/* =================================================================
   3. STYLES CHO MÀN HÌNH LỚN (DESKTOP)
   - Chỉ ghi đè hoặc thêm những thuộc tính cần thiết cho màn hình lớn.
   - Áp dụng khi chiều rộng màn hình từ 768px trở lên.
   ================================================================= */
@media screen and (min-width: 768px) {
    .container {
        max-width: 768px; /* Thay đổi kích thước container */
        min-height: 480px; /* Giữ nguyên hoặc thay đổi nếu cần */
        margin: 0; /* Bỏ margin để canh giữa bằng flexbox của .LoginRegis */
    }

    .LoginRegis {
        align-items: center; /* Canh giữa container theo chiều dọc */
        padding-top: 0; /* Bỏ padding top vì đã canh giữa */
    }

    /* Ẩn/hiện các animation cho desktop */
    .dotlottie-login1 { 
        display: block; /* Hiện lại trên desktop */
    }
    .dotlottie-logina {
        display: none; /* Ẩn đi trên desktop */
    }
    .dotlottie-login { 
        display: block;
        position: relative;
        z-index: 1000;
        width: 100px;
        height: 100px;
        left: 135px;
        top: 20px;
    }
    .dotlottie-register { 
        display: block;
        position: relative;
        z-index: 1000;
        width: 100px;
        height: 100px;
        left: 520px;
        top: 20px;
    }
    .success-after-login {
        left: 210px;
    }

 
