@media screen and ( min-width: 768px)  { 
    .ctnuser{
          width: 100%;
          min-height: 1200px ;
         font-family: Arial, Helvetica, sans-serif;
          background-image : url(' http://dongnaiart.edu.vn/wp-content/uploads/2022/01/hinh-nen-powerpoint-nen-trang-1.jpg');
          background-size: cover;
    }
    .user1 {
        width: 100%;
        height: 240px;
      
        color: black;
        text-align: center;
      

    }
    .user1 img { 
        width: 200px;
        height: 200px;
        border-radius: 50px;
    }

    .user2 {
        text-align: center;
         width: 100%;
         min-height:  300px;
    }
    .sodu {
        background-color: #029578;
        width: 350px;
        height: 100px;
        text-align: center;
        position: relative;
        bottom: 10px;
        left: 37%;
        border-radius: 30px;
    }
    .sodu p {
        color: white;
        position: relative;
        top : 40%; 

        font-weight : bold;
        font-size :25px;
        
    }
    .tableuser {
         width: 100%;
         min-height: 200px;
       
         
    }
    .user2 strong {
         color: black ;
         font-size: 30px;
       
    }
    .tableuser tr th {
        height: 50px;
         border : 1px solid gray;
       font-size: 20px;
         color: white;
         background-color: #029578;
    }
    .tableuser tr td {
       font-size: 25px;
       font-weight: bold;
        border: 1px solid  gray;
        color:black;
        background-color: white;

    }
    .tableuser svg {
     width: 30px;
     height: 30px;
   }
   
    .user3 {
         width: 100%;
        height: 700px;
        text-align: center;
    }
    .user3 img{
        position: relative;
      
        width: 500px;
        height:500px;
     }
     .user h3 {
      font-weight: bold; 
     }
     .changeuser { 
        color : white;
        position: relative;
        left: 25%;
        width: 600px;
        border-radius: 30px; 
        height: 300px;
        background-color: #029578;
     }
     .logout{ 
        width: 100%;
        height: 50px;
        margin-top : 20px;
        background-color: #029578;
     }
  
}

@media screen and (max-width: 767px ) {
    .ctnuser{
          width: 100%;
          height: 1200px ;
          font-family: Georgia, serif;
          background-image : url(' http://dongnaiart.edu.vn/wp-content/uploads/2022/01/hinh-nen-powerpoint-nen-trang-1.jpg');
          background-size: cover;
    }
    .user1 {
        width: 100%;
        height: 200px;
      
        color: black;
        text-align: center;
      

    }
    .user1 img { 
        width: 150px;
        height: 150px;
        border-radius: 50px;
    }

    .user2 {
        text-align: center;
         width: 100%;
         min-height:  200px;
    }
    .sodu {
        background-color: #029578;
        width: 250px;
        height: 50px;
        text-align: center;
        position: relative;
        left: 15%;
        bottom: 10px;
        border-radius: 30px;
    }
    .sodu p {
        color: white;
        position: relative;
        top : 30%; 

        font-weight : bold;
        font-size :20px;
        
    }
    .tableuser {
         width: 100%;
         min-height: 100px;
       
         
    }
    .user2 strong {
         color: black ;
       
    }
    .tableuser tr th {
         border : 1px solid gray;
         height: 50px;
         color: white;
         background-color: #029578;
    }
    .tableuser tr td {
        border: 1px solid  gray;
        color:black;

    }
    .user3 {
         width: 100%;
        height: 700px;
        text-align: center;
    }
    .user3 img{
        position: relative;
        left: 5%;
        width: 300px;
        height:300px;
     }
     .user h3 {
      font-weight: bold; 
     }
     .changeuser { 
        color : white;
        position: relative;
        left: 10%;
        width: 300px;
        border-radius: 30px; 
        height: 300px;
        background-color: #029578;
     }
     .logout{ 
        width: 100%;
        height: 50px;
        margin-top : 20px;
        background-color: #029578;
     }

    
   
}
