
.parentad {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 8px;
    background-color: antiquewhite;

}
    


.div3ad {
    grid-row: span 3 / span 3;
}
/* 
       MÁY TÍNH  */
@media screen and (min-width: 768px) {
    .parentad { 
        width: 100%; 
        max-height: 1500px;
        background-color:rgb(250, 215, 244);
    }
    .div1ad h1 { 
        text-align: center;
        font-family: Arial, Helvetica, sans-serif;
        position: relative;
        top : 5px;
    }
    .div1ad ul { 
        list-style: none;
        width: 100%;
        height: 150px;
        display: flex;
        justify-content: center ;
        align-items: center;
    }
    .div1ad ul li { 
        width: 350px;
        height : 100px;
       box-shadow: 2px 2px 10px 2px gray;
        margin-right: 15px;
        border-radius : 10px;
        text-align: center ;
        font-size : 15px;
        font-weight: bold ;
    }
    .div1ad p { 
        font-size :15px;
        text-align: center;
        font-weight: bold ;
        
    }
     /* DIV2 */

     .listButton { 
        width: 100%;
        max-height: 500px;
        box-shadow:  2px 2px 10px 2px gray;
        display :  flex;
        border-radius : 20px;
        flex-direction: column;
        gap: 10px
     }
     .listButton button:hover{ 
        transform: scale(1.1);
        transition: 0.5s;
        background-color: aliceblue;
        color : black;
        box-shadow: 1px 1px 5px 1px gray;

     }
     .themkhoahoc ul li {
         list-style: none;
         width : 120px;
         height: 40px;
         border: 1px solid black;
         display: flex;
         float: left;
         justify-content: center;
         margin-left: 20px;
        position: relative;
        top: 40px;
        left: 50px;
        border-radius: 30px;
        font-weight: bold;
        background-color: brown;
        color: white;
     }
     #Listkhoahoc {
         position: absolute;
         width: 100%;
         min-height: 200px;
         background-color:#FF4B2B;
         top: 900px;
         
         
     }
 
    
}
        

