@media screen  and  (max-width: 768px)  { 
    .containeCate { 
        width: 100%;
        min-height: 600px;
        display:  grid;
        grid-template-columns: 1fr;
    }
   
    .cateMenu { 
        position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000; /* Đảm bảo header hiển thị trên cùng */
  background: white; /* Thêm màu nền nếu cần */
     }
     .contaithann { 
    
        width: 100%;
        max-height: 400px;
        display : grid;
        grid-template-columns: 1fr ;
        grid-template-rows: 1fr 1fr;
     }


     .listCate {
         width: 100%;
     }
     .categorys  { 
        width: 100%;
        max-height: 400px;
        margin-top: 100px;
        display: grid; 
        place-content: center;
        grid-gap: 10px;
        grid-template-columns:  1fr 1fr 1fr;
       
     }
    
     .category button { 
     width: 110px;
     height: 70px;
     box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
     display: flex;
     justify-content: center;
     transform: 3s ease-in-out;
    
    }

    .category button:hover { 
      
        background-color :#FF792F;
        font-weight: bold;
        
    }
     .titleCate { 
        position: absolute;
        font-size: 9px;
        font-weight: bold;
       width: 50px;
       height: 50px;
       
     }
  
     .category  { 
      margin-top: 20px;
     width: 70px;
     height: 50px;
     background-color: rgb(92, 102, 102 , 0) ;
     }
     .titlelistcourse { 
     
        width: 100%;
        text-align: center;
        height: 50px;
        font-weight: bold;
    }

    .Courses {
        position: relative;
        right: 10%;
        width : 100%;
        min-height: 400px; 
        display: grid; 
        grid-template-columns: 1fr ;
        grid-gap: 10px;
        place-items: center;
       
    }
    .Courses li { 
        width: 100%;
        max-height: 400px;
        overflow: auto;
        list-style: none;
        margin-top:  50px;
        border-radius: 0px 30px 0px 0px;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;

   
    }
    
    .Courses img { 
        width: 100%;
        height : 200px;
       
        background-size: cover;
    }

    .title-course p { 
        font-size: 20px;
        font-weight: bold;
        text-align: center;
    }

    .author-course p {
        font-size : 12px;
        font-weight: bold;
    }
    .author-course img  { 
        width: 170px;
        height: 90px;
        position: relative;
       
    }

    .sale-course p { 
        text-decoration: line-through;
        font-size : 20px;
        font-weight: bold;
        color : red;
    }
    .price-course p { 
        font-size: 15px;
        font-weight: bold;
        color : rgb(29, 216, 113);
        font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }
    .btn-course button { 
      position: relative;
     
    }
    .icon-course i { 
       
    position: absolute;
    color: #FF792F;
    font-size: 30px; 
    }

   
 
    


}

